.modal-franchise {
    // position: fixed;
    // top: 50%;
    // left: 50%;
    // transform: translate(-50%, -50%) scale(1);
    min-width: 280px;
    width: calc(100% - 40px);
    // height: 560px;
    // max-height: 100vh;
    // overflow: auto;
    background-color: var(--primary-text-color);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    padding: 28px; 
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 2px solid var(--button-background-default);
    border-radius: 10px;
    @include mobile {
        width: 440px;
    }

    @include tablet {
        width: 568px;
        height: 672px;
        padding: 32px 58px;
        display: flex;
            flex-direction: column;
            align-items: center;
    }

    @include desktop {
    }
}
.modal-products__close-btn {
        position: absolute;
        top: 12px;
        right: 12px;
        @include tablet {
            top: 20px;
            right: 20px;
        }
            width: 20px;
            height: 20px;
            transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1), transform 300ms cubic-bezier(0.4, 0, 0.2, 1);
            background-color: transparent;
        
            &:hover,
            &:focus {
                fill: var(--button-background-hover);
                transform: scale(2);
            }
}

