        //     background: none;
        //     cursor: url(../images/cursor.svg), auto;
        //     border: none;

.modal-franchise__icon {
    position: relative;
    top: 2px;
    left: 1px;
}
.modal-franchise__img {
    top: 10px;
        right: 10px;
        width: 30px;
        height: 30px;
        
        fill: var(--secondary-text-color);
        
    
        
}
.modal-franchise__img:hover,
        .modal-franchise__img:focus {
            fill: var(---button-background-hover);
        }
.modal-franchise__text {
    font-family: 'Seymour One';
    width: 220px;
        font-weight: 400;
        font-size: 16px;
        line-height: 1.5;
        color: var(--secondary-text-color);
        margin-bottom: 20px;
        
        text-align: center;
        @include tablet {
                font-size: 20px;
                line-height: 1.4;
                margin-bottom: 24px;
                width: 322px;
            }
        
        
        
}
.modal-franchise__description {
    font-size: 14px;
        line-height: 1.4;
        color: var(--secondary-text-color);
        margin-bottom: 24px;
        display: flex;
        align-items: center;
        font-weight: 400;
    @include tablet {
            
            font-size: 16px;
            margin-bottom: 32px;
        }
}

// ---------------- Форма------------------

.modal-franchise-form {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 12px;
    @include tablet {
        align-items: center;
        }
}

.modal-franchise-form__wrap {
    position: relative;
}

.modal-franchise-form__input {
        width: 100%;
        height: 32px;
        padding-left: 10px;
        border: 1px solid var(--primary-background-color);
        background-color:    rgba(248, 210, 78, 0.1);
        border-radius: 10px;
        outline: none;
        transition: border-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
        font-weight: 400;
        font-size: 12px;
        line-height: 1.4;
        color: var(--secondary-text-color);
        @include tablet {
            width: 368px;
            height: 42px;
            font-size: 14px;
            line-height: calc(20 / 14);
        }
}
.modal-franchise-form__input:hover,
.modal-franchise-form__input:focus {
    border: 2px solid var(--button-background-hover);
}
.modal-franchise-form__input::placeholder {
    color: var(--secondary-text-color);
}
.modal-franchise-form__label {
    display: none;
    position: absolute;
    top: 11px;
    left: 10px;
    font-weight: 400;
    color: var(--secondary-text-color);  
    font-size: 12px;
    line-height: calc(17 / 12);    
    @include tablet {
        font-size: 14px;
        line-height: calc(20 / 14);     
    }
}

.modal-franchise-form__comment {
        padding-top: 10px;
        padding-left: 10px;
        width: 100%;
        height: 64px;
        resize: none;
        font-size: 12px;
        line-height: 1.4;
        @include tablet {
            font-size: 14px;
            line-height: calc(20 / 14);            
        }
        color: var(--secondary-text-color);
        border: 1px solid var(--primary-background-color);
        background-color: rgba(248, 210, 78, 0.1);
        border-radius: 10px;
        outline: none;
        transition: border-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
        @include tablet {
            width: 368px;
            height: 82px;
        }
        &::placeholder {
            color: var(--secondary-text-color);
        } 
        &:hover,
        &:focus {
            border: 2px solid var(--button-background-hover);
        }
}

.modal-franchise-form__input:not(:placeholder-shown)+.modal-franchise-form__label,
.modal-franchise-form__comment:not(:placeholder-shown)+.modal-franchise-form__label {
            display: block;
            @media screen and (max-width: 767px) {
                top: -11px;
                color: var(--primary-background-color);
                font-weight: 400;
                font-size: 10px;
                line-height: calc(12 / 10);
            }
            @include tablet {
                left: -80px;
            }
}

.modal-franchise-form__button {
        display: flex;
        justify-content: center;
        color: var(--secondary-text-color);
        background-color: var(--button-background-default);
        box-shadow: 0px 4px 4px rgb(0 0 0 /0.15);
        border-radius: 10px;
        font-family: inherit;
        font-weight: 600;
        font-size: 14px;
        line-height: 1.29;
        letter-spacing: 0.06em;
        text-align: center;
        padding: 12px 40px;
        width: 114px;
        height: 42px;
        margin-left: auto;
        margin-right: auto;
        margin-top: 12px;
        @include tablet {
            margin-top: 20px;
            width: 121px;
        }
}
    
.modal-franchise-form__button:hover {
    background-color: var(--button-background-hover);
}
    
.modal-franchise-form__button:focus {
    color: #fff;
    fill: #fff;
    background-color: var(--button-background-hover);
}
.modal-franchise-form__button:disabled {
    background-color: #ABABAB;
}
